
import MarqueeDouble from 'react-double-marquee';
import './Marquee.css'
const Marquee = (props) => {
    return <div  className='MarqueeMedia' >
        <MarqueeDouble>
            {props.text} <span className={props.span}>{props.text2}</span>   {props.text} <span className={props.span}>{props.text2}</span>  {props.text} <span className={props.span}>{props.text2}</span>

        </MarqueeDouble></div>
}

export default Marquee