import './CarouselItem.css'

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import travelanywhere from '../../img/1.png'
import planner from '../../img/2.png'
import button from '../../img/3.png'
import sneakers from '../../img/4.png'
import calendar from '../../img/5.png'
import Slider from "react-slick";
import { useState, useEffect } from 'react';

export default function SimpleSlider() {
    const arrayLink = [[calendar,'calendar'], [planner,'planner'], [sneakers,'sneakers'], [button,'button']]


    const [windowDimensions, setWindowDimensions] = useState(window.innerWidth);
    useEffect(() => {
        function handleResize() {
            console.log(window.innerWidth)
            setWindowDimensions(window.innerWidth);
        }

        window.addEventListener('resize', handleResize);

    }, [windowDimensions])


    var settings = {

        infinite: true,
        speed: 500,
        slidesToShow: windowDimensions < 600 ? 1 : 3,
        slidesToScroll: 1,
        centerPadding: '40px',
    }

    return (
        <Slider {...settings}>
            <div className='carouselItemContainer'>
                <div className='carouselItem'>
                    <a href='https://www.travelanywherekey.com/' target='_blant'>
                        <img className='projectPhoto' src={travelanywhere} />

                    </a>

                </div>

            </div>

            {arrayLink.map((link) =>
         
                <div className='carouselItemContainer'>
                    <div className='carouselItem'>
                        <a href={`https://mariarass.github.io/${link[1]}/`} target='_blant'>
                            <img className='projectPhoto' src={link[0]} />

                        </a>

                    </div>

                </div>



            )}





        </Slider>
    );
}